.artistsContainer {
  background: linear-gradient(228.48deg, #f2ec3b 20.3%, #80f9c0 96.91%);
}
.leftArtist {
  left: 0;
  top: 10%;
  width: 25%;
  position: absolute;
}
.rigthArtist {
  right: 0;
  top: 80%;
  width: 25%;
  position: absolute;
}
.centerArtist {
  top: 35%;
  left: 50%;
  width: 20vw;
  position: absolute;
  transform: translate(-50%, -50%);
}
.artistDetials {
  width: 50%;
  z-index: 3;
  margin: 0 auto;
  position: relative;
  padding: 35% 0 5% 0;
}
.ArtistGalleryBox1 {
  width: 16vw;
  padding: 0.7rem;
  margin: 0.7rem 0;
  text-align: left;
  position: relative;
  border-radius: 10px;
  transform: rotate(20deg);
  background-color: #fff;
}
.artistProfile {
  top: 20%;
  left: 50%;
  position: absolute;
}
.vegitable {
  width: 10vw;
  right: -15%;
  bottom: -25%;
  position: absolute;
}
.artistDetialName {
  font-weight: bolder;
}
.artistDetialDetail {
  font-size: 1.5rem;
  font-weight: bolder;
  font-family: Raleway;
}
.artistDetialRow {
  display: flex;
  justify-content: space-between;
}
.artistDetialRowTxt {
  margin: 0;
  font-weight: bolder;
  font-family: Raleway;
}
.artistDetialRowTxt1 {
  margin: 0;
  color: #ff4c00;
  font-weight: bolder;
  letter-spacing: 2px;
  font-family: Raleway;
  text-decoration-line: underline;
}
.artistsIconsRow {
  display: flex;
}
.artistsIco {
  width: 1.5rem;
  margin-left: 0.5rem;
  object-fit: contain;
}
.backRow {
  display: flex;
  cursor: pointer;
}
.backArow {
  width: 0.6rem;
  margin-right: 5px;
  object-fit: contain;
}

@media screen and (max-width: 980px) {
  .artistDetials {
    width: 60%;
    padding: 45% 0 5% 0;
  }
}

@media screen and (max-width: 760px) {
  .artistDetials {
    width: 80%;
    padding: 50% 0 5% 0;
  }
  .artistDetialName {
    font-size: 4.5vw;
  }
  .artistDetialDetail {
    font-size: 2.5vw;
  }
  .artistProfile {
    top: 15%;
  }
  .centerArtist {
    top: 25%;
    left: 50%;
    width: 20vw;
    position: absolute;
    transform: translate(-50%, -50%);
  }
  .vegitable {
    width: 10vw;
    right: -10%;
    bottom: -15%;
    position: absolute;
  }
  .ArtistGalleryBox1 {
    width: 20vw;
    padding: 0.5rem;
    margin: 0.5rem 0;
    text-align: left;
    position: relative;
    border-radius: 10px;
    transform: rotate(20deg);
    background-color: #fff;
  }
}

@media screen and (max-width: 460px) {
  .artistProfile {
    top: 10%;
    left: 40%;
  }
  .centerArtist {
    top: 20%;
    left: 40%;
    width: 30vw;
    position: absolute;
    transform: translate(-50%, -50%);
  }
  .vegitable {
    width: 20vw;
    right: -15%;
    bottom: -15%;
    position: absolute;
  }
  .ArtistGalleryBox1 {
    width: 25vw;
    padding: 0.5rem;
    margin: 0.5rem 0;
    text-align: left;
    position: relative;
    border-radius: 10px;
    transform: rotate(20deg);
    background-color: #fff;
  }
  .vegitable {
    width: 15vw;
    right: -15%;
    bottom: -15%;
    position: absolute;
  }
  .artistDetials {
    width: 95%;
    padding: 70% 0 5% 0;
  }
  .artistDetialRowTxt {
    margin: 0;
    font-weight: bolder;
    font-family: Raleway;
    font-size: 3.5vw;
  }
  .artistDetialRowTxt1 {
    font-size: 3.5vw;
  }
  .backArow {
    width: 2vw;
  }
  .artistDetialDetail {
    font-size: 3.5vw;
  }
  .ArtistGalleryBox1 {
    width: 35vw;
    padding: 0.5rem;
    margin: 0.5rem 0;
    text-align: left;
    position: relative;
    border-radius: 10px;
    transform: rotate(20deg);
    background-color: #fff;
  }
}
